import { Github, ExternalLink, Video } from "lucide-react"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import Image from "next/image"
import { useState } from "react"

interface ProjectCardProps {
  project: {
    id: number
    author: {
      name: string
      avatar: string
      username: string
    }
    title: string
    description: string
    tags: string[]
    images: string[]
    video?: string
    githubUrl: string
    liveUrl: string
  }
}

export function ProjectCard({ project }: ProjectCardProps) {
  const [selectedMedia, setSelectedMedia] = useState<string>(project.images[0] || project.video || "")

  return (
    <Card className="bg-card border-border overflow-hidden">
      <CardContent className="p-0">
        <div className="flex flex-col md:flex-row">
          {/* Left Section - Text Content */}
          <div className="flex-1 p-5">
            {/* Author Info */}
            <div className="flex items-center gap-3 mb-4">
              <Avatar className="h-10 w-10">
                <AvatarImage src={project.author.avatar || "/placeholder.svg"} />
                <AvatarFallback>{project.author.name[0]}</AvatarFallback>
              </Avatar>
              <div>
                <p className="font-medium text-foreground">{project.author.name}</p>
                <p className="text-sm text-muted-foreground">{project.author.username}</p>
              </div>
            </div>

            {/* Title */}
            <h3 className="text-xl font-semibold text-foreground mb-3">{project.title}</h3>

            {/* Description */}
            <p className="text-muted-foreground text-sm leading-relaxed mb-4">{project.description}</p>

            {/* Action Buttons */}
            <div className="flex items-center gap-3">
              <Button variant="outline" size="sm" className="gap-2 border-border hover:bg-muted hover:text-gray-400 bg-transparent " asChild>
                <a href={project.githubUrl} target="_blank" rel="noopener noreferrer">
                  <Github className="h-4 w-4" />
                  GitHub
                </a>
              </Button>
              <Button variant="outline" size="sm" className="gap-2 border-border hover:bg-muted hover:text-gray-400 bg-transparent" asChild>
                <a href={project.liveUrl} target="_blank" rel="noopener noreferrer">
                  <ExternalLink className="h-4 w-4" />
                  Live Link
                </a>
              </Button>
            </div>
          </div>

          {/* Right Section - Media */}
          <div className="md:w-80 p-5 flex flex-col gap-3">
            {/* Large Media Block */}
            <div className="aspect-video rounded-md overflow-hidden bg-muted border border-border flex items-center justify-center">
              {selectedMedia.endsWith(".mp4") || selectedMedia === project.video ? (
                <video src={selectedMedia} controls className="w-full h-full object-cover" />
              ) : (
                <Image src={selectedMedia} alt="Selected media" className="w-full h-full object-cover" width={320} height={180} />
              )}
            </div>

            {/* Vertical Thumbnails */}
            <div className="pt-4 flex gap-2 overflow-x-auto">
              {project.images.map((img, index) => (
                <div
                  key={index}
                  className={`w-20 h-20 rounded-md overflow-hidden border cursor-pointer ${selectedMedia === img ? "border-foreground" : "border-border"}`}
                  onClick={() => setSelectedMedia(img)}
                >
                  <Image src={img} alt={`Thumbnail ${index + 1}`} className="w-full h-full object-cover" width={80} height={80} />
                </div>
              ))}
              {project.video && (
                <div
                  className={`w-20 h-20 rounded-md overflow-hidden border cursor-pointer flex items-center justify-center ${selectedMedia === project.video ? "border-foreground" : "border-border"}`}
                  onClick={() => setSelectedMedia(project.video!)}
                >
                  <Video className="h-6 w-6 text-muted-foreground" />
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Bottom Section - Tags */}
        <div className="p-5 border-t border-border bg-muted/30">
          <p className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-2">Tags</p>
          <div className="flex flex-wrap gap-2">
            {project.tags.map((tag) => (
              <Badge key={tag} variant="secondary" className="bg-secondary text-secondary-foreground text-xs">
                {tag}
              </Badge>
            ))}
          </div>
        </div>
      </CardContent>
    </Card>
  )
}
