"use client";

import { useEffect, useState } from "react";
import { Header } from "@/components/header";
import { Sidebar } from "@/components/sidebar";
import { ProjectCard } from "@/components/project-card";

const sampleProjects = [
  {
    id: 1,
    author: {
      name: "Sarah Johnson",
      avatar: "/professional-woman-avatar.png",
      username: "@sarahj",
    },
    title: "E-Commerce Dashboard",
    description:
      "A modern e-commerce dashboard with real-time analytics, inventory management, and order tracking. Built with React and Node.js for optimal performance.",
    tags: ["React", "Node.js", "MongoDB"],
    images: [
      "/generic-data-dashboard.png",
      "/analytics-chart.png",
      "/generic-mobile-app.png",
    ],
    githubUrl: "https://github.com",
    liveUrl: "https://example.com",
  },
  {
    id: 2,
    author: {
      name: "Alex Chen",
      avatar: "/professional-man-avatar.png",
      username: "@alexc",
    },
    title: "AI Content Generator",
    description:
      "An AI-powered content generation tool that helps create blog posts, social media content, and marketing copy using advanced language models.",
    tags: ["Python", "OpenAI", "FastAPI"],
    images: [
      "/futuristic-ai-interface.png",
      "/text-editor.png",
      "/content-preview.png",
    ],
    githubUrl: "https://github.com",
    liveUrl: "https://example.com",
  },
  {
    id: 3,
    author: {
      name: "Maya Patel",
      avatar: "/professional-woman-developer-avatar.png",
      username: "@mayap",
    },
    title: "Task Management App",
    description:
      "A collaborative task management application with real-time updates, team workspaces, and productivity insights. Features drag-and-drop functionality.",
    tags: ["Next.js", "Prisma", "PostgreSQL"],
    images: ["/kanban-board.png", "/task-list.jpg", "/team-dashboard.png"],
    githubUrl: "https://github.com",
    liveUrl: "https://example.com",
  },
];

export default function HomePageClient() {
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [latest, setLatest] = useState<any[]>([]);
  const [stats, setStats] = useState({
    total: 0,
    month: 0,
    activeMin: 0,
    avgPerDay: 0,
  });

  useEffect(() => {
    const load = async () => {
      try {
        const sessRes = await fetch("/api/auth/session");
        const sess = await sessRes.json();
        const res = await fetch(
          `/api/projects?limit=5${
            sess?.user?.id ? `&author=${sess.user.id}` : ""
          }`
        );
        const projects = await res.json();
        setLatest(projects);

        if (sess?.user?.id) {
          const allRes = await fetch(
            `/api/projects?author=${sess.user.id}&limit=100`
          );
          const all = await allRes.json();
          const total = all.length;
          const month = all.filter(
            (p: any) =>
              new Date(p.createdAt).getMonth() === new Date().getMonth() &&
              new Date(p.createdAt).getFullYear() === new Date().getFullYear()
          ).length;
          const activeMin = total * 30;
          const daysInMonth = new Date(
            new Date().getFullYear(),
            new Date().getMonth() + 1,
            0
          ).getDate();
          const avgPerDay = Math.round((month / daysInMonth) * 100) / 100;
          setStats({ total, month, activeMin, avgPerDay });
        }
      } catch {}
    };
    load();
  }, []);

  return (
    <div className="pt-10 min-h-screen bg-background">
      <Header onMenuClick={() => setSidebarOpen(!sidebarOpen)} />
      <Sidebar isOpen={sidebarOpen} onClose={() => setSidebarOpen(false)} />

      <main className="pt-16 px-4 md:px-8 lg:px-12">
        <div className="grid grid-cols-1 lg:grid-cols-12 gap-6">
          <div className="hidden lg:block lg:col-span-3">
            <div className="sticky top-24 bg-card border border-border rounded-xl p-6 shadow-lg h-[500px]">
              <h2 className="text-xl font-semibold mb-4">
                Your Activity Insights
              </h2>

              <div className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div className="p-4 bg-muted/30 rounded-lg">
                    <p className="text-sm opacity-70">Total Projects</p>
                    <p className="text-2xl font-bold">{stats.total || 18}</p>
                  </div>
                  <div className="p-4 bg-muted/30 rounded-lg">
                    <p className="text-sm opacity-70">This Month</p>
                    <p className="text-2xl font-bold">{stats.month || 4}</p>
                  </div>
                  <div className="p-4 bg-muted/30 rounded-lg">
                    <p className="text-sm opacity-70">Active Time (min)</p>
                    <p className="text-2xl font-bold">
                      {stats.activeMin || 620}
                    </p>
                  </div>
                  <div className="p-4 bg-muted/30 rounded-lg">
                    <p className="text-sm opacity-70">Avg / Day</p>
                    <p className="text-2xl font-bold">
                      {stats.avgPerDay || 21}
                    </p>
                  </div>
                </div>
              </div>

              <div className="mt-6">
                <p className="text-sm mb-1">Monthly upload progress</p>
                <div className="w-full bg-muted rounded-full h-2">
                  <div
                    className="bg-primary h-2 rounded-full"
                    style={{ width: "40%" }}
                  ></div>
                </div>
                <p className="text-xs opacity-70 mt-2">4 / 10</p>
              </div>
            </div>
          </div>

          <div className="col-span-12 lg:col-span-6 space-y-6">
            {(latest.length ? latest : sampleProjects).map((project: any) => (
              <ProjectCard
                key={project.id || project._id}
                project={{
                  id: project.id || project._id,
                  author: {
                    name: project.author?.name || "Unknown",
                    avatar: project.author?.image || "/placeholder-user.jpg",
                    username: "@user",
                  },
                  title: project.title,
                  description: project.description,
                  tags: project.tags || [],
                  images: project.images?.length
                    ? project.images
                    : sampleProjects[0].images,
                  githubUrl: project.githubUrl || "#",
                  liveUrl: project.liveUrl || "#",
                }}
              />
            ))}
          </div>

          <div className="hidden lg:block lg:col-span-3">
            <div className="sticky top-24 bg-card border border-border rounded-xl p-6 shadow-lg h-[500px]">
              <h2 className="text-xl font-semibold mb-4">
                Monthly High Activity
              </h2>

              <div className="space-y-4">
                {[
                  { name: "Sarah Johnson", count: 12 },
                  { name: "Alex Martinez", count: 9 },
                  { name: "Priya Shah", count: 7 },
                  { name: "John Doe", count: 5 },
                  { name: "Emily Carter", count: 4 },
                ].map((user, index) => (
                  <div
                    key={index}
                    className="flex items-center justify-between"
                  >
                    <div className="flex items-center gap-3">
                      <span className="text-lg font-bold opacity-70">
                        #{index + 1}
                      </span>
                      <div>
                        <p className="font-medium">{user.name}</p>
                      </div>
                    </div>
                    <p className="text-sm opacity-70">{user.count} projects</p>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
